html {
  height: 100%;
}
body {
  width: 1920px;
  height: 1080px;
  color: #fff;
  font-family: '微软雅黑';
  background: url(images/body-bg.jpg) no-repeat;
  background-size: 100% 100%;
}  
.container {
  width: 270px;
  height: 300px;
  position: absolute;
  top: 639px;
  left: 822px;
  input {
    border: none;
    outline: none;
    color: #21b3b4;
    font-size: 20px;
    margin-left: 5px;
    background: none;
    margin-top: 12px;
  }
  div {
    margin-top: 12px;
  }
  .pik-login {
    background: url(./images/pki-btn.png) no-repeat;
    width: 270px;
    height: 84px;
    display: inline-block;
    cursor: pointer;
    margin: 0;
  }
  .username {
    background: url(./images/username.png) no-repeat;
    height: 44px;
    width: 270px;
    margin-top: 5px;
    display: inline-block;
    input {
      width: 162px;
      padding-left: 95px;
      height: 42px;
      margin: 0;
      color: #21b3b4;
    }
  }
  .password {
    background: url(./images/password.png) no-repeat;
    width: 175px;
    height: 44px;
    padding-left: 95px;
  }
  .login-btn {
    background: url(./images/login.png) no-repeat;
    width: 261px;
    height: 40px;
    cursor: pointer;
  }
}
.error {
  position: absolute;
  color: red;
  bottom: 5px;
  padding-left: 5px;
  text-align: center;
  width: 90%;
}

input:-webkit-autofill , textarea:-webkit-autofill, select:-webkit-autofill {  
    -webkit-text-fill-color: #ededed !important;  
    -webkit-box-shadow: 0 0 0px 1000px transparent  inset !important;  
    background-color:transparent;  
    background-image: none;  
     transition: background-color 1s ease-in-out 0s;  /*背景色透明  生效时长  过渡效果  启用时延迟的时间 */
}  
input {  
     background-color:transparent; 
} 


